"use client";

import { useEffect, useState } from "react";

import type { WaitlistStats } from "./waitlist-constants";
import { WAITLIST_CONFIG, fetchWaitlistStats } from "./waitlist-constants";

/**
 * Hook to fetch and manage waitlist statistics
 */
export function useWaitlistStats() {
  const [stats, setStats] = useState<WaitlistStats>({
    registeredCount: 0,
    remaining: WAITLIST_CONFIG.maxSpots,
    maxSpots: WAITLIST_CONFIG.maxSpots,
    isFull: false,
  });
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<Error | null>(null);

  useEffect(() => {
    async function loadStats() {
      try {
        setIsLoading(true);
        setError(null);
        const data = await fetchWaitlistStats();
        setStats(data);
      } catch (err) {
        setError(err instanceof Error ? err : new Error("Unknown error"));
        console.error("Failed to load waitlist stats:", err);
      } finally {
        setIsLoading(false);
      }
    }

    loadStats();

    // Refresh every 30 seconds
    const interval = setInterval(loadStats, 30000);

    return () => clearInterval(interval);
  }, []);

  return { stats, isLoading, error };
}

