"use client";

import * as React from "react";

import { cn } from "@trading-platform/ui";

interface CircularProgressProps {
  value: number; // Number of registered items (0-max)
  max?: number; // Maximum spots (default 50)
  size?: number; // Size of the circle in pixels
  strokeWidth?: number; // Width of the stroke
  showValue?: boolean; // Whether to show the maximum waitlist spots in the center
  className?: string;
}

function CircularProgress({
  value,
  max = 50,
  size = 32,
  strokeWidth = 3,
  showValue = true,
  className,
}: CircularProgressProps) {
  const remaining = Math.max(0, max - value);
  const percentage = Math.min(100, Math.max(0, (value / max) * 100));

  // SVG circle calculations
  const center = size / 2;
  const radius = (size - strokeWidth) / 2;
  const circumference = 2 * Math.PI * radius;
  const offset = circumference - (percentage / 100) * circumference;

  return (
    <div className={cn("relative inline-flex", className)}>
      <svg
        width={size}
        height={size}
        className="-rotate-90 transform"
        style={{ width: size, height: size }}
      >
        {/* Background circle (light gray) */}
        <circle
          cx={center}
          cy={center}
          r={radius}
          fill="none"
          stroke="currentColor"
          strokeWidth={strokeWidth}
          className="text-black/10"
        />
        {/* Progress circle (black) */}
        <circle
          cx={center}
          cy={center}
          r={radius}
          fill="none"
          stroke="currentColor"
          strokeWidth={strokeWidth}
          strokeDasharray={circumference}
          strokeDashoffset={offset}
          strokeLinecap="round"
          className="text-black transition-all duration-300"
        />
      </svg>
      {/* Center text showing maximum waitlist spots */}
      {showValue && (
        <span className="absolute inset-0 flex items-center justify-center text-xs font-semibold text-black">
          {remaining}
        </span>
      )}
    </div>
  );
}

export { CircularProgress, type CircularProgressProps };
